import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
//
import { MatPaginator, MatSort } from '@angular/material';
import { Observable } from 'rxjs/Observable';
import { debounceTime } from 'rxjs/operators';
import { Subscription } from 'rxjs';
//
import { ErrorHandlingService } from '../../../../../error-handling/services/error-handling.service';
import { HandledError } from '../../../../../error-handling/models/handled-error';
//
import { CategoriesService } from "../../services/categories.service";
import { Category, CategoriesListResponse } from "../../models/category";

const errorKey = 'Error';

@Component({
    selector: 'categories-table',
    templateUrl: './categories-table.component.html',
    styleUrls: ['./categories-table.component.scss']
})
export class CategoriesTableComponent implements OnInit {

    displayedColumns: string[] = [
        'name',
        'actions'
    ];

    filter: FormGroup;

    filterValueChanges: Subscription;

    @ViewChild(MatPaginator) paginator: MatPaginator;

    @ViewChild(MatSort) sort: MatSort;

    totalLength: number = 0;

    categoriesList: Subscription;

    categories: Array<Category> = [];

    constructor(
        public categoriesService: CategoriesService,
        public errorHandlingService: ErrorHandlingService
    ) {
    }

    ngOnInit() {
        this.filter = this.createFilterFormGroup();
        this.filterValueChanges = this.filter.valueChanges.pipe(debounceTime(500)).subscribe(change => this.onFilter());
        this.paginator.pageIndex = 0;

        // Begin observing style list changes.
        this.categoriesList = this.categoriesService.categoriesList.subscribe((categoriesList: any) => {
            this.totalLength = categoriesList.dataCount;
            this.categories = categoriesList.data;
            if (this.categories.length === 0 && this.totalLength > 0 && this.categoriesService.previousPageSize > 0) {
                this.categoriesService.previousPageIndex =
                    Math.ceil(this.totalLength / this.categoriesService.previousPageSize) - 1;
                this.categoriesService.reloadCategories().subscribe(response => {
                    this.categoriesService.categoriesList.next(response);
                },
                    (error: HandledError) => this.errorHandlingService.handleUiError(errorKey, error));
            }
        });
    }


    ngAfterViewInit() {
        this.loadPage();
    }

    ngOnDestroy() {
        this.categoriesList.unsubscribe();
        this.filterValueChanges.unsubscribe();
    }

    createFilterFormGroup() {
        let group: any = {};
        group['name'] = new FormControl('');
        return new FormGroup(group);
    }

    loadPage() {
        this.categoriesService.getCategories(
            Object.assign({}, this.filter.value),
            this.sort.active, this.sort.direction,
            this.paginator.pageIndex, this.paginator.pageSize).subscribe((response: CategoriesListResponse) => {
                this.categoriesService.categoriesList.next(response);
            },
                (err: HandledError) => {
                    this.errorHandlingService.handleUiError(errorKey, err)
                });
    }

    onFilter() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onSort() {
        this.paginator.pageIndex = 0;
        this.loadPage();
    }

    onPage() {
        this.loadPage();
    }

}

